<template>
  <div class="timelineframe">
    <ul class="timeline" >
      <li v-for="(day,index) in mapList" :class="day.type =='label'? 'time-label':''"  >
        <span v-if="day.type =='label'" class="bg-red" >{{day.time}}</span>
        <i class="fa bg-aqua" v-if="day.type =='title'" >D{{day.day}}</i>
        <div class="timeline-item" v-if="day.type =='title'" >
          <div class="timeline-header">
            <h3>{{day.name}}</h3>
            <p>{{day.tip}}</p>
          </div>
        </div>
        <i :class="['fa', 'fa-circle-o','fa-small']" v-if="day.type !='title' && day.type !='label'" ></i>
        <div class="timeline-item" v-if="day.type =='stitle'" >
          <div class="timeline-header">
            <h3>{{day.time}} {{day.name}}</h3>
            <p>{{day.tip}}</p>
          </div>
        </div>
        <div class="timeline-item" v-if="day.type =='subway'" >
          <div class="timeline-body">
            从{{day.start}} {{day.name}},大约需要{{day.time}}到达 {{day.end}}
          </div>
        </div>
        <div class="timeline-item" v-if="day.type =='point'" >
          <div class="timeline-header">
            <h3>{{day.name}}</h3>
            <p>{{day.tip}}</p>
          </div>
          <div class="timeline-body">
            <img :src="day.map" alt="" class="timeline-img" >
          </div>
        </div>
        <div class="timeline-item" v-if="day.type =='tip'" >
          <div class="timeline-body text-gray">
            <p>tip: {{day.tip}}</p>
            <p v-if="day.time" >行程大概{{day.km}}km,建议游玩时间:{{day.time}}</p>
          </div>
        </div>
        <div class="timeline-item" v-if="day.type =='scenic'" >
          <div class="timeline-header">
            <h3>{{day.name}}</h3>
            <p>{{day.tip}}</p>
          </div>
          <div class="timeline-body">
            <img v-if="day.map.length > 0"  v-for="img in day.map" class="timeline-img" :src="img" alt="">
            <p>{{day.info}}</p>
            <p>到达路线图</p>
            <img :src="day.point" alt="" class="timeline-img" >
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      mapList: [{
        time: '2018年5月3日 周四',
        type: 'label'
      },{
        day: '1',
        name: '灵隐 - 梅家坞 - 宋城',
        tip: '今天的主题宋城',
        type: 'title'
      },{
        name: '今日游览路线',
        tip: '从灵隐景区出发沿着梅灵路到宋城体验宋城千古情',
        type: 'point',
        map: 'https://dovejun.github.io/css/images/10.png'
      },{
        name: '飞来峰-灵隐寺',
        info: '灵隐景区有三个有名的寺庙和一座山(飞来峰、灵隐寺、永福寺、韬光寺),而这三个寺庙所在的范围被称为天竺.整个景区充满灵气是一个让人放松身心的地方.很多人去灵隐只为爬山。推荐爬山线路：灵隐寺-上天竺-九龙八塔-天门山-十里锒铛-五云山-林海亭-九溪。',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610555798&di=0475fe55c9276948964f340c74dc1264&imgtype=0&src=http%3A%2F%2Fg.hiphotos.baidu.com%2Fzhidao%2Fwh%253D450%252C600%2Fsign%3D17661826c4cec3fd8b6baf71e3b8f809%2F2e2eb9389b504fc27b1655efe2dde71191ef6d8b.jpg'],
        tip: '飞来峰门票价格45,灵隐寺额外收费30',
        point: 'https://dovejun.github.io/css/images/06.png',
        time: '2-3小时',
        type: 'scenic'
      },{
        name: '梅家坞',
        info: '梅家坞茶文化村位于杭州西湖区，是西湖龙井茶一级保护区和主产地之一，也是杭州城郊最富茶乡特色的农家自然村落及茶文化休闲观光旅游区，有“十里梅坞”之称。梅家坞茶文化村以周总理纪念室、乾隆遗迹、古树观赏点、茶园观赏区、农居群、十里锒铛、古井、古桥、垂钓区、茶艺表演区、小牙坞等十余处景点构建了一个山水情和人世情完美结合的茶文化休闲旅游区。',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610694105&di=5b80c9af372f23088db5b83348df8d48&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimgad%2Fpic%2Fitem%2F9345d688d43f8794686a8403d91b0ef41bd53a02.jpg'],
        tip: '从灵隐寺往南沿着梅灵北路就能到,可以做121路公交车',
        point: 'https://dovejun.github.io/css/images/11.png',
        time: '1-2小时',
        type: 'scenic'
      },{
        name: '云栖竹径',
        info: '位于五云山南麓的云栖坞里，为林木茂盛的山坞景观，素以深山古寺，竹径磬声称胜。 康熙皇帝曾四到云栖，赋诗题额，并赐一株大竹名为“皇竹”，浙江地方官为此建“御书亭”、“皇竹亭”以记盛事.竹径按清康熙时的御道原状修建，以“绝”、“幽”、“雅”为特色，是赏绿、休闲的好地方。',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610739396&di=f882b780e95ffe84be1f6e14bfea0e2d&imgtype=0&src=http%3A%2F%2Fk.zol-img.com.cn%2Fdcbbs%2F22152%2Fa22151165_s.jpg'],
        tip: '',
        point: 'https://dovejun.github.io/css/images/12.png',
        time: '1-2小时',
        type: 'scenic'
      },{
        name: '宋城',
        info: '杭州宋城景区是中国大陆人气最旺的主题公园（由国际主题公园及景点行业权威组织TEA评选），年游客逾600万人次。秉承建筑为形，文化为魂的经营理念，园区内宋河东街、土豪家族、胭脂巷、非来巷、美食街、市井街六大主题街区华丽升级，热闹非凡；',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610803866&di=37780244532d411a842bb0d8e50c3fe4&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimage%2Fc0%253Dpixel_huitu%252C0%252C0%252C294%252C40%2Fsign%3D3c5a6f138d18367ab984779d470beeb8%2F622762d0f703918f2ce79ff05a3d269759eec4d2.jpg'],
        tip: '宋城最精彩的莫过于宋城千古情的演出，尤其是宋城千古情场面太震撼了。建议网上买票,能便宜点.',
        point: 'https://dovejun.github.io/css/images/13.png',
        time: '3-6小时',
        type: 'scenic'
      },{
        tip: '行程结束,回家好好休息.',
        type: 'tip'
      },{
        time: '2018年5月4日 周五',
        type: 'label'
      },{
        day: '2',
        name: '西湖风景',
        tip: '绕湖游,不会太累,天气阴,人应该不多 适合散步游',
        type: 'title'
      },{
        name: '今日游览路线',
        tip: '围西湖绕一圈,然后去城市阳台看灯光秀',
        type: 'point',
        map: 'https://dovejun.github.io/css/images/09.png'
      },{
        name: '龙翔桥',
        tip: '从地铁C1口出站向右经过一个十字路口再向前直走就到西湖景区第一个景点 - 音乐喷泉(先找一下附近有个电子屏,上面写了今天的喷泉是什么时间,下午回来再看),然后向右方向走就开始绕湖游',
        type: 'point',
        map: 'https://dovejun.github.io/css/images/01.png'
      },{
        tip: '这段路旁边的树上会有松鼠,可以喂食,走到断桥附近有个麦当劳 如果不想逛累了被西湖里面的餐馆宰的话可以买点 饿了再吃',
        km: '1.5',
        time: '20-30分钟',
        type: 'tip'
      },{
        name: '断桥',
        info: '据说，早在唐朝，断桥就已建成，时人张祜题杭州孤山寺诗中就有“断桥”一词。桥畔有“云水光中”水榭和断桥残雪碑亭。 白蛇传中白娘子与许仙的相会之桥，古时桥上有门，门上有檐，下雪时中间一段的雪都在门檐上，桥上只有两头有雪，远远望去桥像断了一样，所以称作断桥。桥堍有御碑亭等亭轩建筑，面临里西湖，与宝石山保俶塔隔湖相对，山塔湖亭桥与湖边桃柳组成一幅如画景色，十分迷人。断桥是通往孤山的必经之路，每当雪后人们纷纷去断桥欣赏西湖雪景，孤山与里西湖银装素裹，格外动人，因称“断桥残雪”。',
        map: ['https://ss0.baidu.com/94o3dSag_xI4khGko9WTAnF6hhy/lbsugc/pic/item/7a899e510fb30f248bc1cccec195d143ac4b03a7.jpg'],
        tip: '断桥右手边有荷花池,可以拍照',
        point: 'https://dovejun.github.io/css/images/02.png',
        time: '5-10分钟',
        type: 'scenic'
      },{
        tip: '断桥直走就是白堤,白堤适合休闲的散步,白堤走完就到浙江省博物馆',
        km: '1.2',
        time: '20-50分钟',
        type: 'tip'
      },{
        name: '浙江省博物馆',
        info: '来杭州必逛的博物馆',
        map: [],
        tip: '博物馆所在的小岛上有很多景点都可以逛一下',
        point: 'https://dovejun.github.io/css/images/03.png',
        time: '1-2小时',
        type: 'scenic'
      },{
        tip: '博物馆逛完往左走有两条路线,看体力走哪条,短一点的是苏堤,长一点的是杨公堤,苏堤上可以划划船',
        km: '3km-4.5',
        time: '2-3小时',
        type: 'tip',

      },{
        name: '苏堤/杨公堤',
        info: '苏堤是北宋元祐五年，诗人苏轼任杭州知州时，疏浚西湖，利用浚挖的淤泥构筑并历经后世演变而形成的，杭州人民为纪念苏东坡治理西湖的功绩，把它命名为“苏堤”.西湖杨公堤位于西湖以西，堤上有六桥，自北向南名为：环璧、流金、卧龙、隐秀、景行、浚源。与西面的苏堤六桥前后呼应，合称为“西湖十二桥”。杨公堤全长3.4公里，北起灵隐路，南至虎跑路，串联起曲院风荷、金沙港、杭州花圃、茅家埠、乌龟潭、浴鹄湾和花港观鱼等著名景点，杨公堤 附近还有黄蔑楼、环湖碧舍、兰苑、景行古桥、赵公堤等23处历史文化景观',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610856098&di=4e5e18741a38ba6297124426129d6a84&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimgad%2Fpic%2Fitem%2Fd0c8a786c9177f3e8af20cc77acf3bc79e3d5632.jpg'],
        tip: '苏堤很短,杨公堤很长,想看景点首选杨公堤',
        point: 'https://dovejun.github.io/css/images/04.png',
        time: '1-2小时',
        type: 'scenic'
      },{
        name: '雷峰塔',
        info: '可以抓白蛇的地方',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610612360&di=8d5d5a44cb2a00fd0c9ca5532d72bd43&imgtype=0&src=http%3A%2F%2Fimgsrc.baidu.com%2Fimgad%2Fpic%2Fitem%2F83025aafa40f4bfb3bd483c9094f78f0f7361822.jpg'],
        tip: '登塔很累,且行且珍惜',
        point: 'https://dovejun.github.io/css/images/07.png',
        time: '1-2小时',
        type: 'scenic'
      },{
        tip: '逛完雷峰塔往左走 时间应该在下午5点或者6点左右,可以沿着西湖边上走,西湖边上有到三潭印月的船 坐上去在湖上逛一圈',
        km: '2km-4',
        time: '1-2小时',
        type: 'tip'
      },{
        name: '音乐喷泉',
        info: '只有半小时,提前去的话旁边有座位可以坐,晚了就只能站着看,而且人也很多',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610890297&di=6dde059fcc8c47d3e0104f4158c8f269&imgtype=0&src=http%3A%2F%2Fimgupload4.youboy.com%2Fimagestore20170626d29b7f7e-d1f5-474c-8892-25453c6afbdc.jpg'],
        tip: '保证手机电量够用,可以多拍拍照, 音乐喷泉 每晚7点和8点各一场,一场半小时',
        point: 'https://dovejun.github.io/css/images/05.png',
        time: '30分钟',
        type: 'scenic'
      },{
        name: '钱江新城灯光秀',
        info: '灯光秀位于杭州CBD钱江新城，由70万盏LED灯组成，分别安装在钱江新城核心区沿岸的30栋高层建筑外立面上，运用声、光、电等现代化的视觉效果配上配以大型音乐配暖、自然山水、人文、建筑及杭州logo等元素，文字、灯光、影像显示在钱塘江沿岸30多幢高楼串成的一幅“巨幕”上，呈现一幅幅具有“中国气派、江南韵味”的画卷。',
        map: ['https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1525610945921&di=dd553237a1d9c6a77dc8010daca28125&imgtype=0&src=http%3A%2F%2Fimg.pconline.com.cn%2Fimages%2Fupload%2Fupc%2Ftx%2Fitbbs%2F1609%2F24%2Fc3%2F27435482_1474674322847_mthumb.jpg'],
        tip: '时间为每周二、五、六18：30、19：30各一场,可以在城市广场看到全景',
        point: 'https://dovejun.github.io/css/images/08.png',
        time: '30分钟',
        type: 'scenic'
      },{
        tip: '西湖不大但是绕一圈还是很感觉很累,',
        type: 'tip'
      },{
        time: '2018年5月5日 周六',
        type: 'label'
      },{
        day: '3',
        name: '西溪湿地公园',
        tip: '今天主要去西溪湿地公园,稍微逛逛不会很累,拍风景不错的地方',
        type: 'title'
      },{
        name: '今日游览路线',
        tip: '就在西溪里面逛,从西区逛到东区',
        type: 'point',
        map: 'https://dovejun.github.io/css/images/14.png'
      },{
        name: '西溪湿地公园',
        info: '',
        map: ['https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=190903541,54347555&fm=27&gp=0.jpg'],
        tip: '公园分为东西两区,东区免费但是商业元素比较多,西区收费 门票费是70左右,西区还可以坐船,坐船费用另出,建议先步行再乘船游',
        point: 'https://dovejun.github.io/css/images/15.png',
        time: '6-8小时',
        type: 'scenic'
      },{
        tip: '3天的行程结束,杭州的比较出名的景点基本逛完,希望不留遗憾',
        type: 'tip'
      },{
        type: 'end'
      }]
    }
  },
  methods: {

  },
  computed: {

  }
}
</script>

<style scoped>
  html {
    background: #ecf0f5;
  }
  .timelineframe {
    background: #ecf0f5;
    padding: 20px 10px;
  }
  .timeline {
    position: relative;
    margin: 0 0 30px 0;
    padding: 0;
    list-style: none;
  }
  .timeline::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    width: 4px;
    background: #ddd;
    left: 31px;
    margin: 0;
    border-radius: 2px;
  }
  .timeline>li {
    position: relative;
    margin-bottom: 10px;
    text-align: initial;
  }
  .timeline>li:before, .timeline>li:after {
    content: " ";
    display: table;
  }
  .timeline>li:after {
    clear: both;
  }
  .timeline>.time-label>span {
    font-weight: 600;
    padding: 5px;
    display: inline-block;
    background-color: #fff;
    border-radius: 4px;
  }
  .timeline>li>.fa, .timeline>li>.glyphicon, .timeline>li>.ion {
    width: 30px;
    height: 30px;
    font-size: 15px;
    line-height: 30px;
    position: absolute;
    color: #666;
    background: #d2d6de;
    border-radius: 50%;
    text-align: center;
    left: 18px;
    top: 0;
  }
  .timeline>li>.fa-small {
    width: 16px;
    height: 16px;
    font-size: 12px;
    line-height: 16px;
    left: 25px;
    color: #eee;
  }
  .timeline>li>.timeline-item {
    -webkit-box-shadow: 0 1px 1px rgba(0,0,0,0.1);
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
    border-radius: 3px;
    margin-top: 0;
    background: #fff;
    color: #444;
    margin-left: 60px;
    margin-right: 5px;
    padding: 0;
    position: relative;
  }
  .timeline>li>.timeline-item>.time {
    color: #999;
    float: right;
    padding: 10px;
    font-size: 12px;
  }
  .timeline>li>.timeline-item>.timeline-header {
    border-bottom: 1px solid #f4f4f4;
    padding: 10px;
  }
  .timeline>li>.timeline-item>.timeline-header> h3{
    margin: 0;
    color: #555;
    font-size: 16px;
    line-height: 1.1;
    margin-bottom: 5px;
  }
  .timeline>li>.timeline-item>.timeline-header> p {
    margin: 0;
    color: #999;
    font-size: 14px;
    line-height: 1.1;
  }
  .timeline>li>.timeline-item>.timeline-header>a {
    font-weight: 600;
    color: #3c8dbc;
  }
  .timeline>li>.timeline-item>.timeline-body, .timeline>li>.timeline-item>.timeline-footer {
    padding: 10px;
  }
  .timeline>li>.timeline-item>.timeline-body {
    font-size: 14px;
    color: #555
  }
  .timeline>li>.timeline-item>.timeline-body>.timeline-img {
    width: 100%;

  }
  .timeline>li>.timeline-item>.text-gray {
    font-size: 14px;
    color: #666;
  }
  .timeline>li>.timeline-item>.text-gray p {
    margin: 5px 0px;
  }

  /* 背景色 */
  .bg-red {
    background-color: #dd4b39 !important;
